<template lang='pug'>
    b-card
      b-form-group(label='–°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤–∏–π —Å–ø–∏—Å–æ–∫ –≤—ñ–∑')
        b-btn(type='button' @click='showEditor') –í—ñ–¥–∫—Ä–∏—Ç–∏ —Ä–µ–¥–∞–∫—Ç–æ—Ä
      b-modal(ref='routesEditor' title='–°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤–∏–π –º–∞—Ä—à—Ä—É—Ç' hide-footer)
        b-form(@submit.prevent.stop='createNewPreset')
          b-form-group(label='–í–≤–µ–¥—ñ—Ç—å –Ω–∞–∑–≤–∞–Ω—É –°–ø–∏—Å–∫—É')
            b-form-input(
              id='presetname'
              type='text'
              v-model='presetName'
              required
              placeholder='–í–≤–µ–¥—ñ—Ç—å –Ω–∞–∑–≤—É —Å–ø–∏—Å–∫—É')
          choose-authors(
            @updateSelectedUsers='updatePresetRoute'
            :selectedUsers='selectedUsers')
          authors-list(:selectedUsers='selectedUsers' @updateSelectedUser='updatePresetRoute')
          b-btn(type='submit') –°–æ–∑–¥–∞—Ç—å –ú–∞—Ä—à—Ä—É—Ç
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data () {
    return {
      selectedUsers: [],
      presetName: ''
    }
  },
  computed: mapGetters('usersStore', ['currentUser']),
  methods: {
    ...mapActions('docsStore', ['createPreset', 'getPresets']),
    showEditor () {
      this.$refs.routesEditor.show()
    },
    updatePresetRoute (selectedUsers) {
      this.selectedUsers = selectedUsers
    },
    createNewPreset (e) {
      const newPreset = {
        title: this.presetName,
        routes: this.selectedUsers,
        group: this.currentUser.groupInvite
      }
      this.createPreset(newPreset)
        .then(response => {
          e.target.reset()
          this.selectedUsers = []
          this.$refs.routesEditor.hide()
          this.getPresets()
        })
    }
  },
  components: {
    chooseAuthors: require('./chooseAuthors'),
    authorsList: require('./authorsList')
  }
}
</script>



