<template lang='pug'>
  b-row
    b-col
      h2.subtitle.mb-2 –°–ø–∏—Å–æ–∫ –≥—Ä—É–ø
      b-tabs(vertical card pills).users-list
        b-tab(v-for='group in groups' :key='group._id' :title='group.name + " –ö–æ—Ä–∏—Å—Ç—É–≤–∞—á—ñ–≤: " + group.users.length')
          b-list-group
            b-list-group-item(
              v-for='user in group.users'
              :key='user._id'
              )
              h4 {{ user.author ? user.author : '–ù–µ –∑–∞—Ä–µ—î—Å—Ç—Ä–æ–≤–∞–Ω' }}
              p.mb-0 –†–æ–ª—å: {{ user.role ? user.role : '–ù–µ –∑–∞—Ä–µ—î—Å—Ç—Ä–æ–≤–∞–Ω' }}
              p.mb-0 –î–∞—Ç–∞ —Äe—î—Å—Ç—Ä–∞—Ü—ñ—ó: {{ toDateString(+user.dateRegistration) }}
              p.mb-0 Email: {{ user.email }}
              p.mb-0 –õ–æ–≥—ñ–Ω: {{ user.login }}
              b-button-group(v-if='user.role === "Admin"').mt-3
                b-button(@click='openSendModal(user._id, user.email)' variant='secondary') –í—ñ–¥–ø—Ä–∞–≤–∏—Ç–∏ –¥–æ—Å—Ç—É–ø–∏ —â–∞ —Ä–∞–∑
                b-button(@click='deleteAdmin(user._id, group._id)' variant='warning') –í–∏–¥–∞–ª–∏—Ç–∏ –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞
    b-modal(
      ref='sendModal'
      title='–í–∫–∞–∂—ñ—Ç—å –ø–æ—á—Ç—É, –Ω–∞ —è–∫—É –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç—å—Å—è –¥–æ—Å—Ç—É–ø'
      @ok='submitSend')
      b-form-group(label='Email –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞')
        b-form-input(type='email' v-model='admin.email')
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../../modulesJs/toDateString'
export default {
  data () {
    return {
      admin: {
        email: '',
        adminId: ''
      }
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['groups'])
  },
  methods: {
    ...mapActions('groupsStore',
      [
        'getAllGroups',
        'removeAdmin',
        'sendInviteAdmin'
      ]),
    toDateString,
    deleteAdmin (adminId, groupId) {
      this.removeAdmin({ adminId, groupId })
    },
    openSendModal (adminId, adminEmail) {
      this.admin = {
        email: adminEmail,
        adminId
      }
      this.$refs.sendModal.show()
    },
    submitSend () {
      this.sendInviteAdmin(this.admin)
        .then(response => this.$emit('alert', response.message))
        .catch(e => this.$emit('alert', e.message))
    }
  },
  created () {
    this.getAllGroups()
  }
}
</script>



