<template lang='pug'>
  div
    h4.mt-3 –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –∑–∞ {{ title }}
    b-form-group(label='–û–±—Ä–∞—Ç–∏ –ø—Ä–æ–º—ñ–∂–æ–∫ –∑—Ä—ñ–∑—É —Å—Ç–∞—Ç–∏—Å—Ç–∏–∫–∏')
      vue-rangedate-picker(
        i18n='EN'
        :month='datepickerConfig.month'
        :shortDays='datepickerConfig.shortDays'
        :captions='datepickerConfig.captions'
        :presetRanges='datepickerConfig.presetRanges'
        @selected='submitStat')
    ol
      li –°—Ç–≤–æ—Ä–µ–Ω–æ —É—Å—å–æ–≥–æ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ –∑–∞ –ø–µ—Ä–∏–æ–¥: {{ docsCreated}}
      li –ü—ñ–¥–ø–∏—Å–∞–Ω–æ —É—Å—å–æ–≥–æ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ –∑–∞ –ø–µ—Ä–∏–æ–¥: {{ docsSigned }}
      li –û—Ç–∫–∞–∑–∞–Ω–æ –¥–æ–∫—É–º–µ–Ω—Ç–æ–≤ –∑–∞ –ø–µ—Ä–∏–æ–¥: {{ docsRejected }}
      li –°–µ—Ä–µ–¥–Ω—ñ–π —á–∞—Å –º—ñ–∂ —Å—Ç–≤–æ—Ä–µ–Ω–Ω—è–º —Ç–∞ —Ñ—ñ–Ω–∞–ª—å–Ω–∏–º –ø—ñ–¥–ø–∏—Å–∞–Ω–Ω—è–º –¥–æ–∫—É–º–µ–Ω—Ç–∞: {{ docsTimingResolve }}
      li –°–µ—Ä–µ–¥–Ω—ñ–π —á–∞—Å –æ–¥–Ω–æ–≥–æ –ø—ñ–¥–ø–∏—Å—É: {{ docsTimingSign }}
    h4 –°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –∑–∞ —Ä–æ–ª—è–º–∏:
    b-table(
      striped
      bordered
      hover
      :fields='usersStat.fields'
      :items='usersStat.items')

</template>

<script>
import VueRangedatePicker from 'vue-rangedate-picker'
import toDateString from '../modulesJs/toDateString'
import { mapGetters, mapActions, mapMutations } from 'vuex'
export default {
  data () {
    return {
      loadedStat: false,
      title: '–≤–µ—Å—å —á–∞—Å',
      datepickerConfig: {
        months: ['C—ñ—á–µ–Ω—å', '–õ—é—Ç–∏–π', '–ë–µ—Ä–µ–∑–µ–Ω—å', '–ö–≤—ñ—Ç–µ–Ω—å', '–¢—Ä–∞–≤–µ–Ω—å', '–ß–µ—Ä–≤–µ–Ω—å', '–õ–∏–ø–µ–Ω—å',
          '–°–µ—Ä–ø–µ–Ω—å', '–í–µ—Ä–µ—Å–µ–Ω—å', '–ñ–æ–≤—Ç–µ–Ω—å', '–õ–∏—Å—Ç–æ–ø–∞–¥', '–ì—Ä—É–¥–µ–Ω—å'],
        shortDays: ['–ü–ù', '–í–¢', '–°–†', '–ß–¢', '–ü–¢', '–°–ë', '–í–°'],
        captions: {
          'title': '–û–±–µ—Ä–∏—Ç—å –ø—Ä–æ–º—ñ–∂–æ–∫',
          'ok_button': '–û–±—Ä–∞—Ç–∏'
        },
        presetRanges: {
          today: () => {
            const n = new Date()
            const startToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() + 1, 0, 0)
            const endToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() + 1, 23, 59)
            return {
              label: '–ó–∞ —Å—å–æ–≥–æ–¥–Ω—ñ',
              active: false,
              dateRange: {
                start: startToday,
                end: endToday
              }
            }
          },
          thisMonth: () => {
            const n = new Date()
            const startToday = new Date(n.getFullYear(), n.getMonth() - 1, n.getDate())
            const endToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() - 1, 0, 0)
            return {
              label: '–ó–∞ –æ—Å—Ç–∞–Ω–Ω—ñ–π –º—ñ—Å—è—Ü—å',
              active: false,
              dateRange: {
                start: startToday,
                end: endToday
              }
            }
          },
          last7days: () => {
            const n = new Date()
            const startToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() - 7, 0, 0)
            const endToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() - 1, 0, 0)
            return {
              label: '–ó–∞ –æ—Å—Ç–∞–Ω–Ω—ñ 7 –¥—ñ–±',
              active: false,
              dateRange: {
                start: startToday,
                end: endToday
              }
            }
          },
          last30days: () => {
            const n = new Date()
            const startToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() - 30, 0, 0)
            const endToday = new Date(n.getFullYear(), n.getMonth(), n.getDate() - 1, 0, 0)
            return {
              label: '–ó–∞ –æ—Å—Ç–∞–Ω–Ω—ñ 30 –¥—ñ–±',
              active: false,
              dateRange: {
                start: startToday,
                end: endToday
              }
            }
          },
          allTime: () => {
            const n = new Date()
            const startToday = new Date(0)
            const endToday = new Date(n.getFullYear(), n.getMonth(), n.getDate())
            return {
              label: '–ó–∞ –≤–µ—Å—å —á–∞—Å',
              active: true,
              dateRange: {
                start: startToday,
                end: endToday
              }
            }
          }
        }
      }
    }
  },
  computed: {
    ...mapGetters('statStore', [
      'docsCreated',
      'docsSigned',
      'docsRejected',
      'docsTimingSign',
      'docsTimingResolve',
      'usersStat'
    ])
  },
  methods: {
    ...mapActions('statStore', ['getDocsStat']),
    ...mapMutations('statStore', ['setRange']),
    toDateString,
    submitStat (dateObj) {
      const start = new Date(dateObj.start).getTime()
      const end = new Date(dateObj.end).getTime()
      this.title = `–ø–µ—Ä—ñ–æ–¥ —Å ${this.toDateString(start)} –ø–æ ${this.toDateString(end)}`
      this.setRange({ start, end })
    }
  },
  created () {
    this.getDocsStat()
      .then(response => {
        this.loadedStat = true
      })
  },
  components: {
    VueRangedatePicker
  }
}
</script>

<style lang='sass' scoped>
.users-list
  max-height: 445px
  overflow-y: scroll
  overflow-x: hidden
ol
  list-style: inside
</style>



