<template lang='pug'>
    b-tabs(v-if='!rejected')
      b-tab(title='–ü–æ–¥–ø–∏—Å–∞–≤—à–∏–µ' v-if='signedAuthors.length')
        b-list-group.signers-list
          b-list-group-item(
            v-for='author in signedAuthors'
            :key='author._id'
            variant='success'
            )
            p.subtitle.subtitle_small {{ author.author }}
            p.subtitle.subtitle_small {{ author.role }}
            p.subtitle.subtitle_small –ß–∞—Å –ø—ñ–¥–ø–∏—Å—É: {{ toDateString(+author.dateSigning) }}
            b-card(v-if='author.comment' class='mt-1')
              p.text –ö–æ–º–µ–Ω—Ç–∞—Ä –ø—ñ–¥–ø–∏—Å–∞–Ω—Ç–∞:
              p.text {{ author.comment }}
      b-tab(title='–í —á–µ—Ä–∑—ñ –Ω–∞ –ø—ñ–¥–ø–∏—Å' v-if='waitingAuthors.length')
        b-list-group.signers-list
          b-list-group-item(
            v-for='author in waitingAuthors'
            :key='author._id'
            variant='primary'
            )
            p.subtitle.subtitle_small {{ author.author }}
            p.subtitle.subtitle_small {{ author.role }}
            b-card(v-if='author.comment' class='mt-1')
              p.text –ö–æ–º–µ–Ω—Ç–∞—Ä –ø—ñ–¥–ø–∏—Å–∞–Ω—Ç–∞:
              p.text {{ author.comment }}
      b-alert( show variant='danger' v-else) –î–æ–∫—É–º–µ–Ω—Ç –≤—ñ–¥–º–æ–≤–ª–µ–Ω –≤ –ø—ñ–¥–ø–∏—Å—ñ –í–∞–º–∏
</template>

<script>
import { mapGetters } from 'vuex'
import toDateString from '../../modulesJs/toDateString'
export default {
  props: {
    rejected: Boolean
  },
  methods: {
    toDateString
  },
  computed: {
    ...mapGetters('docsStore', ['document']),
    signedAuthors () {
      if (!this.document.routes) return []
      return this.document.routes.filter(route => route.status === 'resolve')
    },
    waitingAuthors () {
      if (!this.document.routes) return []
      return this.document.routes.filter(route => route.status === 'waiting')
    }
  }
}
</script>
<style lang="sass" scoped>
.signers-list
  max-height: 300px
  overflow-y: scroll
</style>



