<template lang='pug'>
.bg
  b-container
    b-card.mb-3
      h2.title –î–æ–∫—É–º–µ–Ω—Ç–∏ –∫ –ø—ñ–¥–ø–∏—Å–∞–Ω–Ω—é
    b-list-group
      b-alert(variant='primary' show v-if='!loaded') –ó–∞–≤–∞–Ω—Ç–∞–∂—É—î—Ç—å—Å—è...
      b-alert(variant='danger' show v-if='loaded && !sortingData.length') –ù–æ–≤–∏—Ö –¥–æ–∫—É–º–µ–Ω—Ç—ñ–≤ –¥–æ —Ä–æ–∑–≥–ª—è–¥—É –Ω–µ–º–∞—î
      router-link(
        v-for='preview in sortingData'
        :key='preview._id'
        :to='"/edms/" + preview._id'
        class='list-group-item preview-item list-group-item-action')
        .preview-item__icon(class='waiting')
            i(class='fa fa-file-text-o' aria-hidden='true')
        h2.preview-item__title {{ preview.title }}
        time.preview-item__date –î–∞—Ç–∞ –ø—É–±–ª—ñ–∫–∞—Ü—ñ—ó: {{ toDateString(+preview.date) }}
        span.preview-item__author –ê–≤—Ç–æ—Ä –ø—É–±–ª—ñ–∫–∞—Ü—ñ—ó: {{ preview.author }}
        span.preview-item__state –ü–æ—Ç–æ—á–Ω–∏–π –º–∞—Ä—à—Ä—É—Ç: {{ preview.state }} / {{ preview.total }}
        span.preview-item__incoming –ß–∞—Å –≤—ñ–¥–∫—Ä–∏—Ç—Ç—è –≤—ñ–∑–∏: {{ incomingDate(preview) }}
</template>
<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      loaded: false
    }
  },
  computed: {
    ...mapGetters('docsStore', ['documents']),
    ...mapGetters('usersStore', ['currentUser']),
    sortingData () {
      if (!this.documents.length) return []
      return this.documents.slice(0).sort((prev, next) => prev.date <= next.date)
    }
  },
  methods: {
    ...mapActions('docsStore', ['getPreviews']),
    ...mapActions('usersStore', ['getCurrentUser']),
    toDateString,
    incomingDate (document) {
      const user = document.routes.find(
        route => route.author === this.currentUser.author
      )
      return this.toDateString(+user.dateIncoming)
    }
  },
  created () {
    this.getPreviews()
      .then(() => {
        this.loaded = true
      })
  }
}
</script>



