<template lang='pug'>
.bg-simple
  b-container
    b-row
      b-col(sm='12' class='mb-3')
        b-card
          h1.title {{ document.title }}
      b-col(sm='12' class='mb-3')
        b-card(v-if='document.author && document.date && document.versions')
          time.text –î–∞—Ç–∞ –ø–µ—Ä—à–æ—ó –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó {{ toDateString(+document.date) }}
          p.text(v-if='document.versions.lenght > 1') –ü–æ—Ç–æ—á–Ω–∞ –≤–µ—Ä—Å—ñ—è –¥–æ–∫—É–º–µ–Ω—Ç–∞ {{ document.versions[0].version }}
          time.text(v-if='document.versions.lenght > 1') –î–∞—Ç–∞ –ø—É–±–ª–∏–∫–∞—Ü—ñ—ó –≤–µ—Ä—Å—ñ—Ö {{ document.versions[0].version }}: {{ document.versions[0].date }}
    b-row
      b-col
        doc-tabs
    b-row
      b-col
        signers-list(:rejected='false')
      // document info
      b-col(sm='12' lg='6')
        b-alert(
          variant='success'
          v-if='document.globalStatus === "resolved"'
          class='mb-0'
          show
          ) –î–æ–∫—É–º–µ–Ω—Ç —É—Å–ø—ñ—à–Ω–æ –ø—ñ–¥–ø–∏—Å–∞, –π–æ–≥–æ –º–æ–∂–Ω–∞ –∑–Ω–∞–π—Ç–∏ —É
          router-link(:to='"/edms/archive/" + document._id') –ê—Ä—Ö—ñ–≤—ñ
        b-alert(
          variant='primary'
          v-if='document.globalStatus === "waiting"'
          class='mb-0'
          show
          ) –î–æ–∫—É–º–µ–Ω—Ç –Ω–∞ —Å—Ç–∞–¥—ñ—ó –ø–µ—Ä–µ–≥–ª—è–¥—É. –ó–∞—Ä–∞–∑ –π–æ–≥–æ –ø–µ—Ä–µ–≥–ª—è–¥–∞—î: {{ closerViewer }}
        div(v-if='document.globalStatus === "rejected"')
          b-alert(
            variant='danger'
            show
            ) –î–æ–∫—É–º–µ–Ω—Ç –≤—ñ–¥–º–æ–≤–ª–µ–Ω –≤ –ø—ñ–¥–ø–∏—Å—ñ. –í–∏ –º–æ–∂–µ—Ç–µ –µ–≥–æ –¥–æ—Ä–æ–±–∏—Ç–∏ –∞–±–æ –∑–∞–∫—Ä–∏—Ç–∏.
            b-card.mb-3
              p.subtitle.subtitle_small {{ document.versions[0].rejectReason }}
          b-button(
            id='toArchive'
            @click='closeDocument'
            size='md'
            class='mr-3 mb-3'
            ) –ó–∞–∫—Ä–∏—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç
          router-link(
            id='editDocument'
            :to='"/edms/myDocumentEdit/" + id'
            class='btn btn-success btn-md mb-3'
            ) –î–æ—Ä–æ–±–∏—Ç–∏ –¥–æ–∫—É–º–µ–Ω—Ç
          b-tooltip(target='editDocument' title='–ü–µ—Ä–µ–π—Ç–∏ –≤ —Ä–µ–¥–∞–∫—Ç–æ—Ä –≤–µ—Ä—Å—ñ–π')
          b-tooltip(target='toArchive' title='–î–æ–∫—É–º–µ–Ω—Ç –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç—å—Å—è –≤ –∞—Ä—Ö—ñ–≤')
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      id: this.$router.currentRoute.params.id
    }
  },
  computed: {
    ...mapGetters('docsStore', ['document']),
    closerViewer () {
      return this.document.routes.find(route => route.status === 'waiting').author
    }
  },
  methods: {
    ...mapActions('docsStore', ['getMyDocumentById', 'closeDocumentById']),
    toDateString,
    closeDocument () {
      this.closeDocumentById(this.id)
    }
  },
  created () {
    this.getMyDocumentById(this.id)
      .catch(e => {
        this.$router.push('404')
      })
  },
  components: {
    docTabs: require('../_common/docTabs'),
    signersList: require('../_common/signersList')
  }
}
</script>



