<template lang='pug'>
  b-container
    h1.title –ü–∞–Ω–µ–ª—å –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞ {{ currentGroup.name }}
    b-tabs.mt-3
      b-tab(title='–°—Ç–≤–æ—Ä–∏—Ç–∏ —Ä–æ–ª—å –≤ –≥—Ä—É–ø—ñ')
        b-row
          b-col
            b-form(@submit.prevent='createUser' class='mb-3 mt-3')

              b-form-group(label='–ü–Ü–ë –≤–ª–∞—Å–Ω–∏–∫–∞')
                b-form-input(
                  type='text'
                  v-model='user.name'
                  placeholder='–§–∞–º—ñ–ª—ñ—è, –ò º–º—è, –ü–æ –±–∞—Ç—å–∫–æ–≤—ñ'
                  required)

              b-form-group(label='–ù–∞–∑–≤–∞ —Ä–æ–ª–∏ –∫–æ—Ä—Å—Ç—É–≤–∞—á–∞')
                b-form-input(
                  type='text'
                  v-model='user.role'
                  required
                  placeholder='–†–æ–ª—å')

              b-form-group(label='–°—Ç—Ä–æ–∫ –¥—ñ—ó —Å–µ—Ä—Ç–∏—Ñ–∏–∫–∞—Ç–∞, –¥—ñ–±')
                b-form-input(
                  type='number'
                  v-model='user.cerTime'
                  placeholder='0 - –Ω–µ–æ–±–º–µ–∂–µ–Ω–æ'
                  required)

              b-form-group(
                label='–í–≤–µ–¥—ñ—Ç—å –ïmail –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞'
                description='–£–≤–∞–≥–∞! –ù–∞ –Ω—å–æ–≥–æ –≤—ñ–¥–ø—Ä–∞–≤–∏—Ç—å—Å—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è —Ç–∞ –∫–ª—é—á—ñ —Ü—Ñ—Ä–æ–≤–æ–≥–æ –ø—ñ–¥–ø–∏—Å—É!')
                b-form-input(
                  type='email'
                  v-model='user.email'
                  required
                  placeholder='example@mail.ua')

              b-button(type='submit') –°—Ç–≤–æ—Ä–∏—Ç–∏
          b-col.mt-3
              h2.subtitle –ö–æ—Ä–∏—Å—Ç—É–≤–∞—á—ñ –≥—Ä—É–ø–∏: {{ currentGroup.name }}
              b-list-group.users-list
                b-list-group-item(v-for='—ÅurrUser in currentGroup.users' :key='—ÅurrUser._id')
                  h4 {{ —ÅurrUser.author }}
                  p.mb-0 –†–æ–ª—å: {{ —ÅurrUser.role }}
                  p.mb-0 –ó–∞—Ä–µ—î—Å—Ç—Ä–æ–≤–∞–Ω: {{ toDateString(+—ÅurrUser.dateRegistration) }}

      b-tab(title='–°—Ç–∞—Ç–∏—Å—Ç–∏–∫–∞ –≥—Ä—É–ø–∏')
        users-stat

    b-modal(ref='infoModal' title='–ü–æ–≤—ñ–¥–æ–º–ª–µ–Ω–Ω—è')
      | {{ info }}
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import toDateString from '../modulesJs/toDateString'
export default {
  data () {
    return {
      user: {
        role: '',
        email: '',
        login: '',
        name: '',
        certTime: 0
      },
      info: ''
    }
  },
  computed: {
    ...mapGetters('groupsStore', ['currentGroup']),
    ...mapGetters('usersStore', ['currentUser'])
  },
  methods: {
    ...mapActions('groupsStore', ['createNewUser', 'getCurrentGroup']),
    toDateString,
    createUser (e) {
      this.createNewUser({ ...this.user, group: this.currentGroup._id })
        .then(response => {
          e.target.reset()
          this.info = response.message
          this.$refs.infoModal.show()
        })
        .catch(e => {
          this.info = e.message
          this.$refs.infoModal.show()
        })
    }
  },
  components: {
    usersStat: require('./usersStat')
  }
}
</script>
<style lang='sass' scoped>
.admin
  padding: 40px 0
</style>



