<template lang='pug'>
  b-row
    b-col
      b-form(@submit.prevent='createGroup' ref='createGroupForm' class='mb-3')
        h2.subtitle.mb-2 –°—Ç–≤–æ—Ä–∏—Ç–∏ –Ω–æ–≤—É –≥—Ä—É–ø—É
        b-form-group(label='–í–≤–µ–¥—ñ—Ç—å –Ω–∞–∑–≤—É –≥—Ä—É–ø–∏')
          b-form-input(
          type='text'
          v-model='groupName'
          required
          placeholder='–ù–∞–∑–≤–∞ –≥—Ä—É–ø–∏')
        b-form-group(
          label='E-mail –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞ –≥—Ä—É–ø–∏'
          description='–£–≤–∞–≥–∞! –ù–∞ —Ü–µ–π –∞–¥—Ä–µ—Å –±—É–¥—É—Ç—å –≤—ñ–¥—ñ—Å–ª–∞–Ω—ñ –¥–∞–Ω—ñ —Ñ–≤—Ç–æ—Ä–∏–∑–∞—Ü—ñ—ó!')
          b-form-input(
            type='email'
            v-model='adminEmail'
            placeholder='example@gmail.com'
            required)
        b-button(type='submit' variant='primary') –°—Ç–≤–æ—Ä–∏—Ç–∏ –≥—Ä—É–ø—É

    b-modal(
      ref='alertModal'
      title='–£–≤–∞–≥–∞!'
      @ok='submitForm')
      h3 –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –¥–∞–Ω–Ω—ã–µ!
      b-form-group(label='–ù–∞–∑–≤–∞ –≥—Ä—É–ø–ø–∏')
        b-form-input(
          v-model='groupName'
          readonly)
      b-form-group(
        label='Email –∞–¥–º—ñ–Ω—ñ—Å—Ç—Ä–∞—Ç–æ—Ä–∞'
        description='–ù–∞ —Ü—é –ø–æ—à—Ç—É –±—É–¥—É—Ç—å –≤—ñ–¥—ñ—Å–ª–∞–Ω—ñ –ª–æ–≥—ñ–Ω —Ç–∞ –ø–∞—Ä–æ–ª—å')
        b-form-input(v-model='adminEmail')

</template>

<script>
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      groupName: '',
      adminEmail: '',
      info: ''
    }
  },
  methods: {
    ...mapActions('groupsStore', ['createNewGroup']),
    createGroup () {
      this.$refs.alertModal.show()
    },
    submitForm () {
      this.createNewGroup({ groupName: this.groupName, adminEmail: this.adminEmail })
        .then(response => {
          this.$refs.createGroupForm.reset()
          this.$emit('alert', response.message)
        })
        .catch(e => this.$emit('alert', e.message))
    }
  }
}
</script>
<style lang="sass" scoped>
.users-list
  max-height: 500px
  overflow-y: scroll
</style>



